import { prisma } from './prisma';
import { Sequelize } from 'sequelize';

// Export prisma client as db for consistency with import patterns
export const db = prisma;

// Re-export prisma for backward compatibility
export { prisma };

// Create Sequelize instance for legacy models
const sequelize = new Sequelize(process.env.DATABASE_URL || 'postgresql://localhost:5432/mssport', {
  dialect: 'postgres',
  logging: process.env.NODE_ENV === 'development' ? console.log : false,
  pool: {
    max: 5,
    min: 0,
    acquire: 30000,
    idle: 10000
  }
});

// Export Sequelize instance for models
export default sequelize;

// Also export db for Prisma compatibility
export { db };